<template>
  <div class="accountMingXi">
    <!-- <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>您现在的位置：账户</el-breadcrumb-item>
        <el-breadcrumb-item>账户明细查询</el-breadcrumb-item>
      </el-breadcrumb>
      <div>
        <span class="mr-40">客户号： ***********</span>
        <span>操作员号：001</span>
      </div>
    </div> -->

    <div class="position-relative" style="margin-top: 10px;">
      <img src="../../assets/mingxi-search.png" alt="">
      <div class="account-scre">***********</div>
      <div class="account-search">{{ companyInfo.number }}</div>
      <div class="account-name">{{ companyInfo.name }}</div>
      <div class="account-location">{{ companyInfo.bank }}</div>
      <div>
        <el-table :data="detailData" border highlight-current-row fit size="mini" @selection-change="chosePaper">
          <el-table-column type="selection" align="center" />
          <el-table-column label="交易时间" prop="created_at" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column label="借方（支取）" prop="jfMoney" align="center"></el-table-column>
          <el-table-column label="贷方（收入）" prop="dfMoney" align="center"></el-table-column>
          <el-table-column label="余额" prop="lastAccount" align="center"></el-table-column>
          <el-table-column label="对方户名" prop="payeeAccountName" align="center"></el-table-column>
          <el-table-column label="对方账号" prop="payeeAccountNum" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column label="对方开户机构" prop="payeeBank" align="center"></el-table-column>
          <el-table-column label="记账日期" prop="jzTime" align="center"></el-table-column>
          <el-table-column label="摘要" prop="sumarry" align="center"></el-table-column>
          <el-table-column label="备注" prop="toUse" align="center"></el-table-column>
        </el-table>
        <div class="position-relative text-center mt-20 mb-20">
          <el-button type="info" size="medium" @click="returnBill" style="background-color: #1f93d5;border-color: #1f93d5;">电子回执打印/预览</el-button>
          <el-button type="info" size="medium" @click="goBackFirst">返回</el-button>
        </div>
      </div>
    </div>
    <!-- <HuiDanDetail  @back="active = 2" :account-data="{}" /> -->
  </div>
</template>

<script>
// import HuiDanDetail from '@/components/HuiDanDetail'
export default {
  name: 'AccountMingXi',
  // components: { HuiDanDetail },
  data () {
    return {
      active: 1,
      detailData: [],
      selectdeVal: []
    }
  },
  computed: {
    companyInfo () {
      return this.$store.state.companyInfo
    },
    payTransferInfo () {
      return this.$store.state.payTransferInfo
    },
    checkResultInfo () {
      return this.$store.state.checkResultInfo
    }
  },
  watch: {
    payTransferInfo () {
      this.getList()
    },
    checkResultInfo () {
      this.getList()
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      this.detailData = this.payTransferInfo.concat(this.checkResultInfo)
    },
    goBackFirst () {
      this.$router.push('/main/account')
    },
    chosePaper (val) {
      this.selectdeVal = val
    },
    returnBill () {
      if (this.selectdeVal.length === 0) {
        this.$message.warning('请选择查询的回单信息')
      } else if (this.selectdeVal.length > 1) {
        this.$message.warning('一次只能查询一条信息')
      } else {
        this.$router.push({ path: '/main/account/account-huidan', query: { id: this.selectdeVal[0].id } })
      }
    }
  }
}
</script>

